import api from '~api'
import config from '~api-config'

let wldwTable = (params) => api.post('/base/company/list.action',config.api,params);//
let addWldw = (params) => api.post('/base/company/create.action',config.api,params);//
let editWldw = (params) => api.post('/base/company/edit.action',config.api,params);//
let updateWldw = (params) => api.post('/base/company/update.action',config.api,params);//
let deleteWldw = (params) => api.post('/base/company/delete.action',config.api,params);//
let qcyfkWldw = (params) => api.post('/base/company/initialCashier/update.action',config.api,params);//
let xyxxWldw = (params) => api.post('/base/company/credit/update.action',config.api,params);//
let addrList = (params) => api.post('/base/shippingaddress/insert/list.action',config.api,params);//
let editAddrList = (params) => api.post('/base/shippingaddress/list.action',config.api,params);//
let addAddr = (params) => api.post('/base/shippingaddress/create.action',config.api,params);//
let deleteAddr = (params) => api.post('/base/shippingaddress/delete.action',config.api,params);//
let editAddr = (params) => api.post('/base/shippingaddress/edit.action',config.api,params);//
let updateAddr = (params) => api.post('/base/shippingaddress/update.action',config.api,params);//

export default {
    wldwTable,
    addWldw,
    editWldw,
    updateWldw,
    deleteWldw,
    qcyfkWldw,
    xyxxWldw,
    addrList,
    editAddrList,
    addAddr,
    deleteAddr,
    editAddr,
    updateAddr
}



